import Vue from "vue";
import Router from "vue-router";
Vue.use(Router);

// 定义路由配置
let routes = [];
let concat = router => {
  routes = routes.concat(router);
};
let mainRouter = {
  path: "/",
  redirect: "/good/list"
};
concat(mainRouter);
import ShopRouter from "@/views/shop/router";
concat(ShopRouter);
import UserRouter from "@/views/user/router";
concat(UserRouter);
export default routes;
